
.anchorjs-link {
  -webkit-transition: all .25s linear;
  transition: all .25s linear;
}
*:hover > .anchorjs-link {
  margin-left: -1.125em !important;
}
figure.highlight {
  margin: 4px 0px;
  padding: 16px;
  border-radius: 8px;
  background-color: #eee;
}
.demo-icon {
  text-align: center;
}
.demo-icon i {
  display: block;
  font-size: 32px;
}
.demo.panel {
  text-align: center;
  font-size: 36px;
}
.docs-clipboard {
  float: right;
  color: #000;
}



